.outside{
    width: 100%;
}
.top{
    text-align: center;
    width: 100%;
    background-color: orange;
    background-image: url("data:image/png;base64,");
    background-repeat: no-repeat;
    background-size: 27px 20px;
    background-position: 9px center;
    height: 35px;
}
.icon-search{
    position: relative;
    top: 1px;
}
.top-in{
    color: white;
    margin: 0 auto;
    border-radius: 5px;
    position: relative;
    top: 5px;
    left: 15px;
    width: 300px;
    height: 25px;
    background-color: rgb(255, 78, 34);
}
.top-in span{
    position: relative;
    top: -2px;
}
.bank{
    width: 100%;
    text-align: center;
}
.bank>img{
    position: relative;
    top: 8px;
}


.detaill{
    margin-top: 10px;
    margin-left: 13px;
    float: left;
    border-radius: 10px;
    height: 260px;
    width: 45%;
    position: relative;
    top: 10px;
    background-color: white;
    overflow: hidden;
}
.detaill .intro{
    position: relative;
    top: 10px;
    left: 5px;
    display: block;
    width: 160px;
}
.detaill .price{
    position: absolute;
    bottom: 10px;
    left: 5px;
    color: rgb(255, 136, 0);
    font-size: 18px;
}
.detaill .small{
    position: absolute;
    bottom: 12px;
    right: 18px;
    font-size: 10px;
    color: gray;
}
.detaill>img{
    width: 100%;
    height: 66%;
}

.detailr{
    margin-top: 10px;
    float: right;
    border-radius: 10px;
    height: 260px;
    width: 45%;
    position: relative;
    right: 10px;
    top: 10px;
    background-color: white;
    overflow: hidden;
}
.detailr .intro{
    position: relative;
    top: 10px;
    left: 5px;
    display: block;
    width: 160px;
}
.detailr .price{
    position: absolute;
    bottom: 10px;
    left: 5px;
    color: rgb(255, 136, 0);
    font-size: 18px;
}
.detailr .small{
    position: absolute;
    bottom: 12px;
    right: 18px;
    font-size: 10px;
    color: gray;
}
.detailr>img{
    width: 100%;
    height: 66%;
}
.space{
    width: 100%;
    height: 100px;
    float: left;
}