.com-top{
    width: 100%;
}
.com-top .shangpin{
    width: 100%;
}
.back{
    background: rgba(0, 0, 0, 0.4);
    width: 35px;
    height: 35px;
    text-align: center;
    line-height: 34px;
    color: #fff;
    position: absolute;
    left: 4%;
    top: 3%;
    border-radius: 50%;
}
.com-bot{
    position: absolute;
    bottom: 0px;
    width: 100%;
    height: 8%;
    background-color: white;
    box-shadow: rgba(0, 0, 0, 0.15) 0 -0.01rem 1.06rem 0;
}
.com-bot .shop{
    position: absolute;
    left: 2%;
    text-align: center;
    width: 10%;
    height: 100%;
    font-size: 12px;
    color: gray;
    margin-top: 4px;
}
.com-bot .wangwang{
    position: absolute;
    left: 13%;
    text-align: center;
    width: 10%;
    height: 100%;
    font-size: 12px;
    color: gray;
    margin-top: 4px;
}
.com-bot .fav{
    position: absolute;
    left: 25%;
    text-align: center;
    width: 10%;
    height: 100%;
    font-size: 12px;
    color: gray;
    margin-top: 4px;
}
.shop span{
    border-top: 10px;
    display: inline-block;
}
.wangwang span{
    border-top: 10px;
    display: inline-block;
}
.fav span{
    border-top: 10px;
    display: inline-block;
}



.bot-left{
    position: absolute;
    right: 32%;
    top: 17%;
    width: 30%;
    height: 65%;
    color: white;
    border-radius: 1.2rem 0 0 1.2rem;
    background-image: linear-gradient(to right, #FFC500, #FF9402);
    text-align: center;
}
.bot-right{
    position: absolute;
    right: 2%;
    top: 17%;
    width: 30%;
    height: 65%;
    color: white;
    border-radius: 0 1.2rem 1.2rem 0;
    background-image: linear-gradient(to right, #FF7A00, #FE560A);
    text-align: center;
}
.bot-left>span{
    line-height: 33px;
    font-weight: bold;
}
.bot-right>span{
    line-height: 33px;
    font-weight: bold;
}